import '../assets/css/notification.css'
import {useState} from 'react';
function Notification(){
    const [noty,setNoty]=useState(false);
    const openNoty=()=>{
        setNoty(true);
    }
    const closeNoty=()=>{
        setNoty(false);
    }
    return(
        <div>
            {
                !noty?
                    <div className="noty-logo cursor-pointer" onClick={openNoty}>
                        <i className="fa fa-bullhorn" style={{transform:"rotate(-25deg)"}}></i>
                    </div>
                :
                <div className="notification-container">
                    <div style={styles.header} className="flex ali-center">
                        MediaBoost Update
                    </div>
                    <div className="flex jtc-center ali-center noty-content">
                        notification
                    </div>
                    <div style={styles.footer} className="flex jtc-center ali-center cursor-pointer">
                        Annoncement kit
                    </div>
                    <div className="close-noty-btn cursor-pointer" onClick={closeNoty}>
                        x
                    </div>
                </div>
            }
        </div>
    )
}
const styles={
    header:{
        width:"100%",
        height:45,
        fontWeight:"bold",
        color:"white",
        background:"linear-gradient(to right, #2bc7d3, #2bd3ce)",
        paddingLeft:20,
        position:"relative"
    },
    footer:{
        backgroundColor:"white",
        height:35,
        fontSize:14,
        color:"gray",
        boxShadow:"rgb(0 0 0 / 18%) 0px 1px 7px 0px"
    }
}
export default Notification;