import '../assets/css/apiContent.css';
import ApiRequestDescription from './ApiRequestDescription';
import ApiResponseDescription from './ApiResponseDescription';
import {useState} from 'react';
function ApiContent(){
    const [addOrder,setAddorder]=useState([
        ["parameters","key","action","service","link","quantity","runs(optional)","interval(optional)"],
        ["Description","Your Api key","add","Service ID","Link to page","Needed quantity","runs to deliver","interval in minutes"]
    ]);
    const addorder_default=[
        ["parameters","key","action","service","link","quantity","runs(optional)","interval(optional)"],
        ["Description","Your Api key","add","Service ID","Link to page","Needed quantity","runs to deliver","interval in minutes"]
    ];
    const addorder_package=[
        ["parameters","key","action","service","link"],
        ["Description","Your Api key","add","Service ID","Link to page"]
    ];
    const addOrder_custom=[
        ["parameters","key","action","service","link","comments"],
        ["Description","Your Api key","add","Service ID","Link to page","Comment list separated by \r\n or \n"]
    ];
    const addOrder_change=((e)=>{
        e.preventDefault();
        const addOrder_value=e.target.value
        if(addOrder_value==="default"){
            setAddorder(addorder_default);
        }else if(addOrder_value==="package"){
            setAddorder(addorder_package);
        }else{
            setAddorder(addOrder_custom);
        }
    })
    const serviceList=[["Parameters","key","action"],["Description","Your API key","services"]];
    const serviceListRes=[
        {
            "service": 1,
            "name": "Followers",
            "type": "Default",
            "category": "First Category",
            "rate": "0.90",
            "min": "50",
            "max": "10000",
            "refill": true
        },
        {
            "service": 2,
            "name": "Comments",
            "type": "Custom Comments",
            "category": "Second Category",
            "rate": "8",
            "min": "10",
            "max": "1500",
            "refill": false
        }
    ]
    const addRes=[{
        "order": 23501
    }]
    const orderStatus=[
                    ["parameters","key","action","order"],
                    ["Description","Your Api key","status","Order Id"]
    ]
    const orderStatusRes={
        "charge": "0.27819",
        "start_count": "3572",
        "status": "Partial",
        "remains": "157",
        "currency": "USD"
    }
    const multipleOrderStatus=[
        ["parameters","key","action","order"],
        ["Description","Your Api key","status","Order Id separated by comma"]

    ]    
    const multipleOrderStatusRes=[{
        "1": {
            "charge": "0.27819",
            "start_count": "3572",
            "status": "Partial",
            "remains": "157",
            "currency": "USD"
        },
        "10": {
            "error": "Incorrect order ID"
        },
        "100": {
            "charge": "1.44219",
            "start_count": "234",
            "status": "In progress",
            "remains": "10",
            "currency": "USD"
        }
    }];
    const createRefill=[
        ["parameters","key","action","order"],
        ["Description","Your Api key","refill","Order Id"]
    ]
    const createRefillRes=[{
        "refill": "1"
    }];
    const getRefill=[
        ["parameters","key","action","order"],
        ["Description","Your Api key","Refill_status","Refill Id"]
    ]
    const getRefillRes=[
        {
            "status": "Completed"
        }
    ]
    const userBalance=[
        ["parameters","key","action",],
        ["Description","Your Api key","balance"]
    ]
    const userBalanceRes=[
        {
            "balance": "100.84292",
            "currency": "USD"
        }
          
    ]
    return(
        <div className="flex jtc-center api-content">
            <div className="main-content">
                <div className="flex flex-col">
                    <div className="content-heading">
                        API
                    </div>
                    <div className="flex flex-col table-container"> 
                        <div className="flex table-row jtc-sp between">
                            <div className="flex ali-center table-data">
                                HTTP Method
                            </div>
                            <div className="flex ali-center table-data">
                                POST    
                            </div>
                        </div>
                        <div className="flex table-row jtc-sp between">
                            <div className="flex ali-center table-data">
                                API URL
                            </div>
                            <div className="flex ali-center table-data">
                                https://mediaboost.in/api/v2
                            </div>
                        </div>
                        <div className="flex table-row jtc-sp between">
                            <div className="flex ali-center table-data">
                                Response format
                            </div>
                            <div className="flex ali-center table-data">
                                JSON
                            </div>
                        </div>
                    </div>
                </div>
                <div>
                    <div className="sub-content-heading">
                        Service list
                    </div>
                    <div>
                        <ApiRequestDescription data={serviceList} type="servicelist"/>
                    </div>
                    <div className="example-heading">
                        Example response
                    </div>
                    <div>
                        <ApiResponseDescription resData={serviceListRes} />
                    </div>
                </div>
                <div>
                    <div className="sub-content-heading">
                        Add Order
                    </div>
                    <div>
                        <select name="sel" className="add-select-container" onChange={addOrder_change}>
                            <option value="default">Default</option>
                            <option value="package">Package</option>
                            <option value="custom">Custom Comment</option>
                        </select>
                    </div>
                    <div>
                        <ApiRequestDescription data={addOrder} type="addOrder"/>
                    </div>
                    <div className="example-heading">
                        Example response
                    </div>
                    <div>
                        <ApiResponseDescription resData={addRes} />
                    </div>
                </div>
                <div>
                    <div className="sub-content-heading">
                        Order Status
                    </div>
                    <div>
                        <ApiRequestDescription data={orderStatus} type="addOrder"/>
                    </div>
                    <div className="example-heading">
                        Example response
                    </div>
                    <div>
                        <ApiResponseDescription resData={orderStatusRes} />
                    </div>
                </div>
                <div>
                    <div className="sub-content-heading">
                        Multiple Order Status
                    </div>
                    <div>
                        <ApiRequestDescription data={multipleOrderStatus} type="addOrder"/>
                    </div>
                    <div className="example-heading">
                        Example response
                    </div>
                    <div>
                        <ApiResponseDescription resData={multipleOrderStatusRes} />
                    </div>
                </div>
                <div>
                    <div className="sub-content-heading">
                        Create Refill
                    </div>
                    <div>
                        <ApiRequestDescription data={createRefill} type="addOrder"/>
                    </div>
                    <div className="example-heading">
                        Example response
                    </div>
                    <div>
                        <ApiResponseDescription resData={createRefillRes} />
                    </div>
                </div>
                <div>
                    <div className="sub-content-heading">
                        Get Refill
                    </div>
                    <div>
                        <ApiRequestDescription data={getRefill} type="addOrder"/>
                    </div>
                    <div className="example-heading">
                        Example response
                    </div>
                    <div>
                        <ApiResponseDescription resData={getRefillRes} />
                    </div>
                </div>
                <div>
                    <div className="sub-content-heading">
                        User Balance
                    </div>
                    <div>
                        <ApiRequestDescription data={userBalance} type="addOrder"/>
                    </div>
                    <div className="example-heading">
                        Example response
                    </div>
                    <div>
                        <ApiResponseDescription resData={userBalanceRes} />
                    </div>
                </div>
            </div>
        </div>
    )
}
export default ApiContent;