import '../assets/css/apiPage.css';
import {useState} from 'react';
function Header(){
    const [mobileMenu,setMobileMenu]=useState(true);
    const openMenu=()=>{
        const menu=document.getElementsByClassName('menu-items');
        if(mobileMenu){
            menu[0].style.height="400%";
            setMobileMenu(false);
        }else{
            menu[0].style.height="0%";
            setMobileMenu(true);
        }
    }
    return (
        <div className="header flex jtc-sp-between">
            <div>
                <img className="logo" src="https://cdn.mypanel.link/7dp9a8/4o10nbu8n2sety9c.png" />
            </div>
            <div className="menu-items flex jtc-sp-around ali-center" style={{fontSize:14}} >
                <div className="cursor-pointer">
                    <i className="fa fa-user icon"></i> Sign in
                </div>
                <div className="cursor-pointer">
                    <i className="fa fa-book icon"></i>Services
                </div>
                <div className="cursor-pointer">
                    <i className="fa fa-link icon"></i>API
                </div>
                <div className="cursor-pointer">
                    <i className="fa fa-user icon"></i>Sign Up
                </div>
                <div className="cursor-pointer">
                    <i className="fa fa-address-card icon"></i>About Us
                </div>
                <div className="cursor-pointer">
                    <i className="fa fa-book icon"></i>Blog
                </div>
            </div>
            <div className="hamurger-icon-mobile cursor-pointer" onClick={openMenu}>
                <i className="fa fa-bars"></i>
            </div>
        </div>
    )
}
export default Header;