import '../assets/css/apiContent.css';
function ApiRequestDescription(props){
    const {data}=props;
    const {type}=props;
    var index=0;
    console.log("data=",data);
    return(
        <div>
            <div className="flex flex-col table-container">
                {
                    data[0].map((item)=>{
                        index++;
                        return <div className="flex table-row jtc-sp between" key={type+index}>
                                    <div className="flex ali-center table-data">
                                        {index!==1?data[0][index-1]:<b>{data[0][index-1]}</b>}
                                    </div>
                                    <div className="flex ali-center table-data">
                                    {index!==1?data[1][index-1]:<b>{data[1][index-1]}</b>} 
                                    </div>
                                </div>
                    })
                }

            </div>
        </div>
    )
}
export default ApiRequestDescription;